.menu {
  font-size: 24px;
  align-self: start;
  color: #b5b7b7;
  border-bottom: 1px solid #48494a;
  width: 100%;

  &__items {
    display: flex;
    gap: 5px;
    margin-bottom: 5px;
  }

  &__item {
    display: flex;
    gap: 5px;
    cursor: pointer;
    white-space: nowrap;
    position: relative;
    transition: color 0.3s ease 0s;
    text-transform: capitalize;

    &:not(:last-child) {
      &::after {
        content: "/";
      }
    }

    &:last-child {
      color: #fff;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -2px;
        width: 10%;
        height: 1px;
        border-radius: 10px;
        background-color: #b5b7b7;
      }
    
      &::after {
        content: "";
        position: absolute;
        left: 10%;
        bottom: -2px;
        width: 90%;
        height: 2px;
        background-color: #fff;
        border-radius: 4px;
        box-shadow: 0px 0px 4px 1px #fade4b;
        transition: all 0.5s;
      }
    }

    &:hover {
      color: #fff;
    }
  }
}